/* Container for the video thumbnail */

.custom-video-container {
    position: relative;
    display: flex;
    /* Use flexbox for alignment */
    flex-direction: column;
    /* Stack items vertically */
    align-items: center;
    /* Center items horizontally */
    justify-content: center;
    /* Center items vertically */
    width: 80%;
    margin: 20px auto;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    background-color: #444;
    /* Optional background for contrast */
    text-decoration: none;
}

/* Thumbnail styling */

.custom-video-thumbnail {
    width: 100%;
    max-height: 500px;
    /* Constrain the height */
    object-fit: cover;
    /* Ensure the image covers the container */
    display: block;
    border-radius: 12px;
}

/* Play button overlay */

.custom-play-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.6);
    border: 2px solid rgba(255, 255, 255, 0.8);
    border-radius: 50%;
    padding: 20px;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
}

.custom-play-button:hover {
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.8);
    transform: translate(-50%, -50%) scale(1.2);
}

/* Play icon styling */

.custom-play-icon {
    font-size: 40px;
    color: white;
    display: inline-block;
    line-height: 1;
    transition: color 0.3s ease;
}

.custom-play-button:hover .custom-play-icon {
    color: black;
}

/* Video title styling */

.custom-video-title {
    margin-top: 15px;
    font-size: 18px;
    font-weight: bold;
    color: #444;
    text-align: center;
    text-decoration: none !important;
}

.load-more-btn {
    background-color: #444;
    border: none;
    border-radius: 30px;
    color: #fff;
    font-size: 11px;
    letter-spacing: 2px;
    padding: 8px 20px;
    text-transform: uppercase;
}

.video-desc {
    margin: auto;
    width: 90%;
}

.video-desc h2 {
    color: #444 !important;
    margin-left: 10px;
}

.video-desc h3 {
    color: #444;
    font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
    font-size: 12px;
    margin-bottom: 0;
    margin-top: 10px;
}

.view-more-btn {
    border: none;
    border-bottom: 1px solid #fff;
    color: #000;
    float: right;
    font-size: 12px;
    background-color: transparent;
}

.related-posts h5 {
    color: #444;
}

.stars-header {
    margin: auto;
    width: 80%;
}

.stars-header img {
    width: 100%;
    border-radius: 40%;
}

.video-icons {
    display: flex;
    justify-content: space-between;
    width: 80%;
    color: #fff;
    margin-top: 10px;
}

.pornstar-name{
    text-decoration: underline;
}

/* Advertisement Card Styles */

@keyframes gradientMove {
    0% { background-position: 0% 50%; }
    100% { background-position: 100% 50%; }
}

@keyframes pulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.08); }
}

.ad-card {
    background: linear-gradient(270deg,#ff4081,#f50057,#ff4081);
    background-size: 400% 400%;
    animation: gradientMove 6s ease infinite;
    color: #fff;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 4px 10px rgba(0,0,0,0.3);
    font-family: sans-serif;
    text-align: center;
    position: relative;
    overflow: hidden;
    transition: transform .3s, box-shadow .3s;
}

.ad-card:hover {
    transform: scale(1.03);
    box-shadow: 0 8px 18px rgba(0,0,0,.4);
}

/* Variant with background image */
.ad-card.with-img {
    background-size: cover;
    background-position: center;
}
.ad-card.with-img::before {
    content: "";
    position: absolute;
    inset: 0;
    background: rgba(0,0,0,0.45);
}

.ad-card-content { /* inner wrapper to stay above overlay */
    position: relative;
    z-index: 1;
}

.cta-pill {
    background: #fff;
    color: #f50057;
    padding: 10px 20px;
    border-radius: 30px;
    display: inline-block;
    font-weight: bold;
    animation: pulse 1.4s infinite;
    margin-top: 10px;
}

@media(max-width:500px) {
    .custom-video-container {
        width: 100%;
    }
}